<template>
    <v-container class="pa-0">
        <div v-if="isUserLoaded">
            <v-btn class="top-left-fixed-button" text icon color="#FFFFFF"
                   @click="$router.go(-1)">
                <v-icon>mdi-close</v-icon>
            </v-btn>

            <v-row style="width: 100%" class="pa-0 ma-0">
                <PublicProfile
                        class="pa-0 ma-0"
                        :profile-image="profileImage"
                        :header-image="headerImage"
                        :username="username"
                        :known-languages="knownLanguages"
                        :languages-to-learn="languagesToLearn"
                        :description="description"
                        :birth-date="birthDate"
                        :avg-rating="avgRating"
                        :num-ratings="numRatings"
                        :my-rating-is-visible="true"
                        @update-user-rating="updateUserRating"
                        :my-rating="myRating"
                        :my-u-i-d="myUID"
                        :uid="uid"
                >
                </PublicProfile>
            </v-row>

            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="blue"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
                </v-btn>
            </v-snackbar>
        </div>
    </v-container>
</template>

<script>
    import PublicProfile from "./Profile/PublicProfile";
    import Constants from "../store/Constants";
    import {mapActions} from 'vuex';

    export default {
        name: "UserProfile",
        data() {
            return {
                myUID: null,
                uid: null,
                profileImage: null,
                headerImage: null,
                username: null,
                knownLanguages: null,
                birthDate: null,
                languagesToLearn: null,
                description: null,
                avgRating: null,
                numRatings: null,
                myRating: null,
                snackbar: false,
                snackbarText: null,
                isUserLoaded: false,
                closeText: Constants.TEXT_CLOSE
            }
        },
        methods: {
            ...mapActions([
                'getUserByUID',
            ]),
            updateUserRating: function (myRatingUpdated) {
                let self = this;

                self.myRating = myRatingUpdated;

                let selectedUser = this.$store.getters.getUserByUID(self.uid);

                self.avgRating = selectedUser.avgRating;
                self.numRatings = selectedUser.numRatings;
            },
        },
        components: {
            PublicProfile,
        },
        beforeMount() {
            let self = this;
            self.myUID = self.$store.getters.getUserInfoUID;
            self.uid = self.$route.params.id;
            self.getUserByUID(self.uid).then(function (activeUser) {
                self.profileImage = activeUser.profileImage;
                self.username = activeUser.username;
                self.birthDate = self.$store.getters.getFormattedDateInTextMonth(activeUser.birthDate.toDate());
                self.languagesToLearn = activeUser.languagesToLearn;
                self.knownLanguages = activeUser.knownLanguages;
                self.description = activeUser.description;
                self.avgRating = activeUser.avgRating;
                self.numRatings = activeUser.numRatings;
                self.myRating = self.$store.getters.getRatingOfUserByUID(self.uid);
                self.headerImage = self.$store.getters.getProfileHeaderImage;
                self.isUserLoaded = true;
            }).catch(function () {
                self.snackbarText = Constants.ERROR_GENERIC_CONNECTION_PROBLEM;
                self.snackbar = true;
            });

        }
    }
</script>

<style scoped>
    .top-left-fixed-button {
        position: fixed;
        top: 0px !important;
        left: 0px !important;
        z-index: 2;
    }
</style>
